body{
  background-color: #ffffff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

h1{
  color: #FCF7F2;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 30px;
  padding: 30px 0 0 20px;
  font-weight: normal;
}

.App{
  background-color: #383E41;
  min-height: 700px;
  width: 375px;
  margin: 50px auto;
  border-radius: 20px;
}

#titleUnderLine{
  height: 3px;
  width: 230px;
  background-color: #FA845B;
  margin-left: 20px;
}

#to-do-form input{
  background-color: #383E41;
  border: 0;
  margin: 20px;
  width: 325px;
  height: 150px;
  font-size: 15px;
  color: #ffffff;
}

#to-do-form input::placeholder{
  color: #C4C4C4;
}

#buttonContainer{
  display: flex;
  justify-content: center;
}

#to-do-form button{
  width: 155px;
  height: 40px;
  border-radius: 15px;
  border: 0;
  margin: 10px;
  font-size: 15px;
}

#addBtn{
  background-color: #FA845B;
  color: #383E41;
}

#cancelBtn{
  background-color: #979797;
  color: #ffffff;
}

#to-do-form input:focus, #to-do-form button:focus{
  outline:none;
}