.list{
background-color: #4D5458;
border-radius: 15px;
width: 335px;
height: 50px;
margin: 10px auto;
color: #ffffff;
}

.list p{
    padding: 15px;
    position: relative;
}

.list p span{
    right: 10px;
    cursor: pointer;
    position: absolute;
}

.faicons{
    margin: 0 5px;
    font-size:15px;
}

.list p input{
    background-color: transparent;
    border: 0;
    color: #ffffff;
    font-size: 15px;
}

.list p input:focus{
    outline: none;
}